.App {
  text-align: center;
  background-color: lightgray;
  font-size: calc(10px + 2vmin);
  color: black;
}

.Container {
  min-height: 100vh;
  /*min-width: 500px;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 3em;
  font-size: 1em;
}

.Loading {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.CTA {
  display: flex;
  flex-direction: column;
  align-items: center;
  /*gap: 1em;*/
  max-width: 25em;
  border-style: solid;
  border-color: gray;
}

.CTA p {
  margin: 0.5em;
}

.CTA .Header {
  font-weight: bold;
  font-size: 1.2em;
}

.CTA .Sub-Text {
  font-size: 0.8em;
  font-style: italic;
}


.ButtonContainer {
  width: 10em;
  height: 2em;
  font-weight: bold;
  border-style: solid;
  box-shadow: 6px 6px 0px 0px rgba(34, 60, 80, 0.2);
  margin: 1em;
}

.Button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: deepskyblue;
  border-color: dodgerblue;
  cursor: pointer;
}

.Button:hover  {
  background-color: lightsalmon;
  border-color: darksalmon;
}

.Fetch-Icon:before {
  content: "üé®";
  margin-right: 0.2em;
}
.Button:hover .Fetch-Icon:before {
  content: "‚ú®";
}

.Login-Icon:before {
  content: "üîí";
  margin-right: 0.2em;
}
.Button:hover .Login-Icon:before {
  content: "üîì";
}


.Button-Loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: lightgrey;
  border-color: grey;
  color: dimgrey;
}
.Loading-Icon:before {
  content: "‚è≥";
  margin-right: 0.2em;
}


.Image {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 25em;
  height: 25em;
  border-style: solid;
  border-color: gray;
}

.Popup {
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 300px;
  position: fixed;
  border-style: solid;
  border-color: gray;
  box-shadow: 8px 8px 0px 0px rgba(34, 60, 80, 0.2);
}